/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/

/*product pages*/

.product-page-header{

  h1{
    font-size: 2.5rem;
    text-align:center;
  }

  p{
    text-align: center;
    font-family: $font__pre;
    margin-bottom: 40px;
  }

.product-types{
  display:flex;
  justify-content: space-between;
  width:340px;
  margin:0 auto;
  padding-bottom: 40px;

  a{
    font-size: 1.2rem;
    text-transform: uppercase;
    color:$color__brand-green;

    &:hover{
      color:$color__brand-purple;
    }

  }

}

.dashed-line{
  border-top: 1px dashed $color__background-hr;
  margin-bottom: 50px;
}


}

/* About Page */

.page-template-about{
  position:relative;
  margin-top: -100px;
}

.site-main.about {
    display: flex;
    flex-direction: column;

    .p1{
      font-family: $font__pre;
      width:800px;
      margin: 0 auto;
      margin-bottom: 20px;
    }

    .about.container {
        display: flex;
        flex-direction: column;
        // margin-bottom: 40px;

        h1 {
            padding: 15px 0;
            text-align: center;

        }
    }

    .about-hero {
        height: 100vh;
        width: 100%;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;

        .about-title {
          color:white;
              font-size: 3rem;
        }
    }
}


/* Front page */
.home h1 {
    font-weight: lighter;
    text-align: center;
    padding: 30px 0;
    font-size: 2.5rem;
    display: block;
    text-align: center;
}

.hero {
    background-image: url('../../images/home-hero.jpg');
    height: 100vh;
    background-size: cover;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.shop-stuff-section {
    display: flex;
    flex-direction: column;
    @include container;

    .shop-stuff-container {
        display: flex;
        flex-direction: row;
        justify-content: space-around;

        .shop-cat-item {
            width: 25%;
            text-align: center;
            width: 260px;
            height: 270px;
            @include top-heavy-border-box;
            padding: 10px 10px 0;

            p {
                font-family: $font__pre;
                height: 75px;
                margin-bottom: 30px;
            }

            &:hover {
                border: 1px solid $color__brand-orange;
                border-top: 5px solid $color__brand-orange;
            }

            img {
                width: 70px;
            }
        }
    }
}

.inhab-newsfeed-section {
    display: flex;
    justify-content: center;
    flex-direction: column;
    @include container;

    .inhab-newsfeed-container {
        display: flex;
        justify-content: space-between;

        .inhab-newsfeed-item {
            width: 32%;
            // paddisng:2px;
            img {
                object-fit: cover;
                width: 100%;
                height: 250px;
            }

            .content-box {
                @include top-heavy-border-box;
                margin-top: -7px;
                height: 170px;
                padding: 20px 10px 0;
                position: relative;

                .entry-meta {
                    font-family: $font__pre;
                    font-size: 0.8rem;
                    color: $color__text-main;
                }

                p {
                    margin: 10px 0 0;
                    text-transform: uppercase;
                    font-weight: bold;
                    font-size: 1.25rem;
                    line-height: 1.5rem;
                    letter-spacing: 3px;
                    color: $color__brand-green;

                    &:hover {
                        color: black;
                    }
                }

                .inhab-button {
                    position: absolute;
                    bottom: 10px;
                    padding-top: 8px;
                    padding-bottom: 8px;
                }
            }
        }
    }
}

.latest-adventures {
    display: flex;
    flex-direction: column;
    height: 820px;
    @include container;

    .adventure-container {
        display: flex;
        height: 540px;

        h2 {
            display: block;
            padding-top: 10%;
        }

        .inhab-button {
            margin-top: 20px;
        }

        .adventure-left {
            width: 50%;
            background-color: darkgrey;
        }

        .adventure-right-container {
            width: 50%;
            background-color: grey;
            display: flex;
            flex-direction: row;
            flex-flow: wrap;

            .adventure-top-right {
                width: 100%;
            }

            .adventure-bottom-left {
                width: 50%;
            }

            .adventure-bottom-right {
                width: 50%;
            }
        }
    }
}
/* Home page*/
.site-main:first-child {
    margin-top: 100px;
}

.container {
    @include container;
    display: flex;
}

.post {
    margin-top: 60px;
    padding-right: 60px;
}

.entry-header {
    position: relative;

    h1 {
        color: white;
    }

    a {
        color: $color__brand-white;
        text-decoration: none;

        &:hover{
          color:$color__brand-yellow;
        }
    }

    .entry-title {
        position: absolute;
        top: 0;
        left: 0;
        margin-top: 30px;
        background-color: $color__brand-green;
        padding: 10px 60px 10px 10px;
        max-width: 560px;
        font-size: 2.2rem;
        line-height: 120%;
        // font-family: $font__main;
        text-transform: uppercase;
    }

    .entry-meta{
      position:absolute;
      right:0px;
      bottom:7px;
      padding: 2px 20px;
      font-size: 1.2rem;
      background-color: $color__brand-green;
      color:$color__brand-white;
    }

    img {
        width: 760px;
    }
}

.entry-content {
    p {
        font-family: $font__pre;
        line-height: 200%;
        width: 760px;
    }
}

.entry-footer{

  font-size: 1.1rem;

  .cat-links, .tags-links{
    a{
      color:$color__brand-green;
    }
  }

  .tags-links{
    padding-left:20px;
  }

  // .tag-links{
  //   a{
  //     color:$color__brand-green;
  //   }
  // }
}


/*single product pages*/

.single-product-article{

display:flex;
flex-direction: row;
flex-wrap: nowrap;

img{
  width: 540px;
  object-fit: cover;
}

.product-info-container{
  width:50%;
  padding-left: 40px;

  .price{
    font-size: 1.9rem;
    margin-top: 0;
    margin-bottom: 0;
  }

  h1{
    margin-top: 0;
    font-size: 2.3rem;
    color:$color__brand-orange;
  }

  p{
    width:auto;
  }


  .entry-footer{
    margin-top: 30px;
  }
}

}

/*find us*/

