}

.site-main{
  padding-right:60px;
}

input, textarea{
    background-color: $color__background-pre;
    border-radius: 0;
    font-family: $font__pre;
    color:$color__text-main;
}

input{
  height:40px;
}


input[type=submit] {
  border:none;
  box-shadow:none;
  text-shadow:none;
  font-size:1rem;
  @extend .green-button-style;
  font-family: $font__main;
}

.asterix{
  color:red;
}

}

.search-results{

  hr{
    margin-right:50px;
  }

h1{
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.entry-title{
  background-color: transparent;
  position:static;
