  p{
    margin-top:30px;
  }

  .inhab-button{
    margin-top: 40px;
  }
}

.search-submit{
  display:none;
}

fieldset{
  border:none;
}

input[type=search]{
  background-color: $color__background-pre;
  border-radius: 0;
  font-family: $font__pre;
  color:$color__text-main;
  padding: 6px;
}

}

.error-404{
width:815px;

h1{
